<template>
	<div>
		<div  @click="drawer = true" :style="{fontSize:size}">
			<slot name="content">
				<icon icon="gd-more"></icon>
			</slot>
		</div>
		<el-drawer
			size="20%"
			append-to-body
			:with-header="false"
			:visible.sync="drawer"
			ref="eldrawer">
			<slot name="drawer-content">
				<operations :formItem="formItem" @templates-dialog='templatesDialogShow' :drawer="ObjDrawer"></operations>
			</slot>
		</el-drawer>
		<!-- <component :is="comp" :show="show" :form="formItem"></component> -->
	</div>
</template>

<script>
	import Operations from './Operations.vue'
	// import TemplatePublish from '../templatePublish/TemplatePublish.vue'
	export default {
		name: 'FormOperation',
		data() {
			return {
				drawer: false,
				ObjDrawer: this.$refs,
				comp: '',
				show: false
			}
		},
		props: {
			formItem: Object,
			size: {
				type: String,
				default: '24px'
			}
		},
		components: {
			Operations,
			// TemplatePublish
		},
		methods: {
			templatesDialogShow() {
				this.comp = 'TemplatePublish';
				this.show = new Boolean(true);
			}
		}
	}
</script>

<style>
	.el-drawer {
		overflow-y: auto;
		outline: none;
	}
</style>
