import {
	copyForm,
	updateFavour,
	updataDataName,
	deleteFormByformId,
	formOpen,
} from "@/network/form.js";
import Router from '@/router/index.js'
import Vue from 'vue'

export function operations(value, formItem) {
	let operationType = value.iconClass;
	if (value.path != null || value.path != undefined) {
		//跳转
		Router.push(value.path + "/" + formItem.formId);
	} else {
		//铭泽修改
		switch (operationType) {
			//复制
			case "gd-fuzhi":
				copyForm(formItem.formId).then((res) => {
					message(res.msg);
					viewType()
				});
				break;
				//收藏
			case "gd-shoucang":
				updateFavour(this.formItem.formId).then((res) => {
					// if (res.code == 20) {
					// 	formItem.formIsFavour = 1;
					// 	this.showData[1][1].text = "取消收藏";
					// } else {
					// 	this.formItem.formIsFavour = 0;
					// 	this.showData[1][1].text = "收藏";
					// }
					this.message(res.msg);
					this.drawer.eldrawer.hide();
					this.viewType()
				});
				break;
				//重命名
			case "gd-bianji":
				this.drawer.eldrawer.hide();
				this.$prompt(
					"<p class='msg'>表单重命名成功，系统内各处将显示重命名标题。此操作不影响公开表单名称</p><p class='content'>新表单名：</p>",
					"重命名", {
						customClass: "message-box-rename",
						confirmButtonText: "确定",
						dangerouslyUseHTMLString: true,
						inputValue: this.formItem.formName,
					}
				).then(({
					value
				}) => {
					if (value == "") {
						this.message("表单名不能为空", "error");
					} else {
						updataDataName(this.formItem.formId, value).then((res) => {
							// console.log(res);
							if (res.code === 24) {
								this.message(res.msg);
								this.formItem.formName = value;
							} else {
								this.message(res.msg, "warning");
							}
						});
					}
				});
				break;
				//删除
			case "gd-shanchu":
				deleteFormByformId(this.formItem.formId).then((res) => {
					this.drawer.eldrawer.hide();
					this.message(res.msg);
					viewType()
				});
				break;
				//发布
			case "gd-fabu":
				// this.drawer.eldrawer.hide();
				if (formItem.formOpen == 0) {
					formOpen(formItem.formId).then(() => {
						formItem.formOpen = 1;
						message("发布表单成功！");
					});
				} else {
					message("表单已经发布", "warning");
				}
				Router.push('/formFill/summarypage/' + formItem.formId)
				break;
		}
	}
}

function message(msg, type) {
	new Vue().$message({
		showClose: true,
		type: type || "success",
		message: msg,
		duration: 2000,
	});
}

function viewType() {
	const viewIndex = this.$route.meta.index;
	if (viewIndex === 1) {
		this.$store.dispatch('form/saveMyform')
	} else {
		this.$store.dispatch('form/saveFavorites')
	}
}
