<template>
	<div class="thumbnail-item" @mousemove="formHover(true)" @mouseleave="formHover(false)">
		<div class="ti-content">
			<div class="ti-CollectionIcon">
				<icon v-if="formData.formIsFavour === 1" icon="gd-favorites"></icon>
			</div>
			<div class="ti-formHeader">
				<div class="ti-title">{{formData.formTitle}}</div>
				<div class="ti-formType" v-if="formType" :style="formType.style">
					{{formType.name}}
				</div>
			</div>
			<div class="ti-formOpen" :style="formOpen.style">
				{{formOpen.name}}
			</div>
			<div class="ti-formIcon">
				<icon icon="gd-form"></icon>
			</div>
		</div>
		<div class="ti-footer">
				<div class="ti__count-data">
					<!-- {{formData.counts}} -->
					<span class="ti__count text-exceeds">{{formData.formDataCount}}</span>
					<span class="ti__text">数据</span>
				</div>			
		</div>
		<div class="ti-footer__focus" v-show="isActive">
			<div class="ti-footer__operations">
				<div 
				v-for="value in operations" 
				:key="value.iconClass"
				@click="operationClick(value)">
					<icon :icon="value.iconClass"></icon>
					<span>{{value.text}}</span>
				</div>
			</div>
			<div class="ti-more">
				<form-operation :formItem="formData"></form-operation>
			</div>
		</div>
	</div>
</template>

<script>
	import FormOperation from '../formOperation/FormOperation'
	import {operations} from '../formOperation/operations.js'
	export default {
		name: 'ThumbnailItem',
		data() {
			return {
				isActive: false,
				operations: [
					{
						iconClass: "gd-bianji",
						text: "编辑",
						path: "/formFill/maincontent",
					},
					{
						iconClass: "gd-fabu",
						text: "发布",
					},
					{
						iconClass: "gd-shuju",
						text: "数据",
						path: "formFill/formdata",
					},
				]
			}
		},
		computed: {
			formType() {
				let formType ={};
				formType.name = this.formData.formType.substring(this.formData.formType.length-2);
				formType.style = {
					color: '',
					background: ''
				}
				if(formType.name === '问卷') {
					formType.style.color = '#83adff';
					formType.style.background = '#eef4ff';
				// }else if(formType.name === '报名') {
				// 	formType.style.color = 'rgba(255,140,0, .8)';
				// 	formType.style.background = 'rgb(255, 255, 0, .1)';
				}else {	//if(formType.name === '表单')
					formType.name = '表单'
					formType.style.color = '#7dd1c2';
					formType.style.background = '#ebf8f6';
				}
				return formType
			},
			formOpen() {
				let formOpen = {
					name: '',
					style: {
						color: ''
					}
				};
				let isformOpen = this.formData.formOpen;
				if(isformOpen === 0) {
					formOpen.name = '未发布';
					formOpen.style.color = '#ffbe3a';
				}else {
					formOpen.name = '发布中';
					formOpen.style.color = '#00bf6f';
				}
				return formOpen;
			}
		},
		components: {
			FormOperation
		},
		props: {
			formData:Object
		},
		methods: {
			formHover(bool) {
				this.isActive = bool;
			},
			operationClick(value) {
				console.log(value)
				operations(value, this.formData)
			}
		}
	}
</script>

<style>
	.thumbnail-item {
		display: flex;
		position: relative;
		flex-direction: column;
		height: 200px;
		padding: 0 14px 14px;
		border-radius: 4px;
		box-shadow: -2px 4px 5px 0 rgba(0,0,0,.06);
		background-color: #FFF;
	}
	.thumbnail-item:hover {
		box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
	}
	.ti-content {
		display: flex;
		flex-direction: column;
		flex: 1;
	}
	.ti-CollectionIcon {
		height: 28px;
		line-height: 28px;
	}
	.gd-favorites {
		display: inline-block;
		font-size: 25px;
		color: rgb(255, 133, 51);
	}
	.ti-formState {
		float: right;
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
	.ti-formHeader {
		height: 20px;
		line-height: 20px;
		display: flex;
		justify-content: space-between;
	}
	.ti-title{
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}
	.ti-formType {
		padding: 0 6px;
		border-radius: 2px;
	}
	.ti-formOpen {
		margin-top: 6px;
		font-size: 12px;
	}
	.ti-formIcon {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 30px;
		text-align: center;
		color: rgb(255, 133, 51);
	}
	.ti-footer {
		height: 30px;
		line-height: 30px;
		display: flex;
		justify-content: space-between;
	}
	.ti__count {
		display: inline-block;
		max-width: 65px;
		vertical-align: bottom;
		font-size: 20px;
		margin-right: 5px;
	}
	.ti-footer__focus {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 12;
		display: flex;
		justify-content: space-between;
		padding: 0 14px;
		width: 100%;
		height: 45px;
		line-height: 45px;
		background-color: #f7f8fa;
		border-radius: 0 0 4px 4px;
	}
	.ti-footer__operations {
		display: flex;
		justify-content: space-between;
	}
	.ti-footer__operations>div {
		margin-right: 10px;
	}
	.ti-footer__operations>div:hover, .ti-more:hover{
		color: var(--color-text-change);
	}
</style>
