<template>
	<div>
	<main-bar :title="viewTitle" @compName="formShowChange"></main-bar>
	<div class="myform-content" v-if="formData == ''">暂无数据</div>
	<div class="myform-content" v-else>
		<!-- <component :is="formDisplayType" :formData="formData"></component> -->
		<layout-thumbnail class="myform-content" v-show="formDisplayType === 'LayoutThumbnail'" :formData="formData"></layout-thumbnail>
		<layout-list v-show="formDisplayType === 'LayoutList'" :formData="formData"></layout-list>
	</div>
	</div>
</template>

<script>
	import MainBar from './MainBar'
	import LayoutThumbnail from './LayoutThumbnail/LayoutThumbnail'
	import LayoutList from './LayoutList/LayoutList'
	
	export default {
		name: 'MyForm',
		data() {
			return {
				viewTitle: this.title,
				formDisplayType: 'LayoutThumbnail'
			}
		},
		props: ['title', 'formData'],
		computed: {
	
		},
		components: {
			MainBar,
			LayoutThumbnail,
			LayoutList
		},
		methods: {
			formShowChange(index) {
				this.formDisplayType = index;
			}
		}
	}
</script>

<style>
	.myform-content {
		margin-top: 20px;
	}
</style>
