<template>
	<div class="main-bar">
		<div class="main-title">{{title}}</div>
		<slot name="right">
			<ul v-if="operations">
				<li>
					<el-tooltip content="切换到列表视图" v-show="index === 'LayoutList'" @click.native="indexClick('LayoutThumbnail')" popper-class="tooltiptext">
						<icon icon="gd-liebiao"></icon>
					</el-tooltip>
					<el-tooltip content="切换到缩略图视图" v-show="index === 'LayoutThumbnail'" @click.native="indexClick('LayoutList')" popper-class="tooltiptext">
						<icon icon="gd-caidan"></icon>
					</el-tooltip>
				</li>
			</ul>
		</slot>
	</div>
</template>

<script>
	export default {
		name: 'MainBar',
		data() {
			return {
				index: 'LayoutThumbnail'
			}
		},
		props: {
			title: {
				type: String,
				default: ''
			},
			operations: {
				type: Boolean,
				default: true
			}
		},
		methods: {
			indexClick(compName) {
				this.index = compName;
				this.$emit('compName', compName);
			}
		}
	}
</script>

<style>
	.main-bar {
		display: flex;
		justify-content: space-between;
		font-size: 18px;
		padding-bottom: 10px;
		border-bottom: 1px solid #ebebeb;
	}
	.main-title {
		font-weight: 500;
	}
	.tooltiptext {
		font-size: 16px;
	}
</style>
