<template>
	<div class="layout-list">
		<el-table
			:data="formData"
			style="width: 100%; font-size: 18px;"
			max-height="100%">	<!-- height="550" -->
			<el-table-column
				fixed
				width="22">
				<template slot-scope="scope">
					<icon v-if="scope.row.formIsFavour === 1" class="li__form-favorites" icon="gd-shoucang_black"></icon>
				</template>
			</el-table-column>
			<el-table-column
				fixed
				show-overflow-tooltip
				label="名称">
				<template slot-scope="scope">
					<icon class="li__form-icon" icon="gd-form"></icon>
					<span>{{scope.row.formTitle}}</span>
				</template>
			</el-table-column>
			<el-table-column
				label="类型"
				width="100"
				align="center"
			>
				<template slot-scope="scope">
					<span>{{scope.row.formType}}</span>
				</template>
			</el-table-column>
			<el-table-column
				label="收集状态"
				width="100"
				align="center"
			>
				<template slot-scope="scope">
					<span style="color: #ffbe3a;" v-if="scope.row.formOpen === 0">未发布</span>
					<span style="color: #00bf6f;" v-else>发布中</span>
				</template>
			</el-table-column>
			<el-table-column
				label="数量"
				width="100"
				align="center">
				<template slot-scope="scope">
					<span>{{scope.row.formDataCount}}</span>
					<span>数据</span>
				</template>
			</el-table-column>
			<el-table-column
				prop="formCreateTime"
				label="创建时间"
				align="center"
				width="200">
			</el-table-column>
			<el-table-column label="操作"
				:fit="false"
				width="280"
				fixed='right'>
				<template slot-scope="scope">
					<div class="layout-list__operations">
						<div
							class="layout-list__operations-item"
							v-for="value in operations" 
							:key="value.iconClass"
							@click="operationClick(value, scope.row)">
								<icon :icon="value.iconClass"></icon>
								<span>{{value.text}}</span>
							</div>
							<form-operation class="li-operation" :formItem="scope.row" ></form-operation><!-- @click.native="formIndexClick(scope.row)" -->
					</div>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	import FormOperation from '../formOperation/FormOperation'
	import {operations} from '../formOperation/operations.js'
	export default {
		name: 'LayoutList',
		data() {
			return {
				operations: [
					{
						iconClass: "gd-bianji",
						text: "编辑",
						path: "/formFill/maincontent",
					},
					{
						iconClass: "gd-fabu",
						text: "发布",
					},
					{
						iconClass: "gd-shuju",
						text: "数据",
						path: "formFill/formdata",
					},
				]
			}
		},
		components: {
			FormOperation
		},
		props: {
			formData:Array
		},
		methods: {
			operationClick(value, formItem) {
				console.log(value)
				operations(value, formItem)
			}
		}
	}
</script>

<style>
.layout-list {
	box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
/* .el-table--enable-row-hover .el-table__body tr:hover > td {
   font-size: 21px !important;
}	 */
.li__form-favorites {
	font-size: 12px;
	color: rgb(255, 133, 51);
}
.li__form-icon {
	font-size: 24px;
	color: rgb(255, 133, 51);
	margin-right: 5px;
}
.layout-list__operations>div {
	margin-left: 20px;
	display: inline-block;
}

.layout-list__operations>div:first-child {
	margin-left: 0;
}
.layout-list__operations-item {
	display: inline-block;
	color: #2672ff;
}
.layout-list__operations-item:hover {
	color: var(--color-btn-focus);
}
.li-operation:hover{
	color: var(--color-text-change);
}
</style>
